/* eslint-disable */
import Vue from 'vue';

import serialService from 'kiwrious-webserial/lib/service/SerialService';
import { SensorReadResult } from "kiwrious-webserial/lib/data/SensorReadResult";
import { SensorDecodedValue } from "kiwrious-webserial/lib/data/SensorDecodedValue";

import SensorValueConductivity from '@/components/sensor/value/SensorValueConductivity/index.vue';
import SensorValue from '@/components/sensor/value/SensorValue/index.vue';
import SessionCard from '@/components/Card/index.vue'
import { SENSOR_TYPE } from 'kiwrious-webserial/lib/service/SerialRawValue';
import { COLOR, UNIT, OBSERVABLE, OBSERVABLE_FULL_NAME, UNIT_SYMBOL } from '../../constants';
import * as moment from 'moment';

let chartData: any[] = [];

let lastDate = 0;
let chartUpdator: number | null | undefined = null;

const XAXISRANGE = 30;//6000;
const XAXISTYPE = 'numeric';//datetime
const YAXIS_LABELS_SIZE = "13px";
const XAXIS_LABELS_SIZE = "13px";
const YAXIS_TITLE_SIZE = "13px";
const LEGEND_TEXT_SIZE = "13px";

window.onbeforeunload = function () {
  return "Data will be lost if you leave the page, are you sure?";
};
interface RecordingData {
  label: string;
  unit: string;
  unitSymbol: string;
  avg: string;
  min: number;
  max: number;
}

interface ChartSeries {
  name: string; 
  data: any[]
}

interface Recording { 
  time: string; 
  duration: number; 
  data: RecordingData[]; 
  rawData: any 
}

export default Vue.extend(
  {
    name: 'Home',
    components: {
      'sensor-value-conductivity': SensorValueConductivity,
      'sensor-value': SensorValue,
      'session-card': SessionCard
    },
    destroyed() {
      if (chartUpdator) {
        clearInterval(chartUpdator);
      }
    },
    mounted() {
      serialService.onSerialData = (decodedData: SensorReadResult) => {
        this.latestValues = decodedData.decodedValues as SensorDecodedValue[];
        this.sensorType = decodedData.sensorType;
        if (!this.colorApplied) {
          if(this.lastConnectedSensorName !== this.sensorType){
            this.resetChart();
            this.lastConnectedSensorName = this.sensorType;
            this.startChartUpdator();
          }
          this.applyColor();
        }
      };
      serialService.onSerialConnection = (connect) => {
        this.isSensorConnected = connect;
        if (!connect) {
          this.colorApplied = false;
          this.latestValues = [];
          if (chartUpdator) {
            clearInterval(chartUpdator)
          }
          if (this.isRecording) {
            this.isRecording = false;
            this.saveRecording();
          }
        } else {
          this.startChartUpdator();
        }
      };

      // window.setInterval(function () {
      //   self.resetData()
      //   chartRef.updateSeries([{data: chart1Data}, {data: chart2Data}], true)
      //   self.series[0].data = chart1Data;
      //   self.series[1].data = chart2Data;
      // }, 60000);

    },
    data() {
      return {
        sampleRate: 0, 
        lastConnectedSensorName: "",
        updatorInterval: 1000,
        resetInterval: 60000,
        colorApplied: false,
        sensorType: "",
        isSensorConnected: false,
        currentSessionStart: null as any,
        isRecording: false,
        latestValues: [] as SensorDecodedValue[],
        options: {
          colors: [COLOR.DEFAULT, COLOR.DEFAULT],
          tooltip: { enabled: false },
          chart: {
            foreColor: COLOR.DEFAULT,
            id: 'kiwrious-chart',
            height: 350,
            type: 'line',
            toolbar: {
              show: false
            },
            zoom: {
              enabled: false
            },
            animations: {
              enabled: true,
              easing: 'linear',
              dynamicAnimation: {
                speed: 1000
              }
            },
          },
          stroke: {
            curve: 'smooth',
            width: 4
          },
          legend: {
            fontSize: LEGEND_TEXT_SIZE,
            labels: {
              colors: [COLOR.DEFAULT]
            },
            showForSingleSeries: true,
            onItemClick: {
              toggleDataSeries: false
            },
            onItemHover: {
              highlightDataSeries: false
            },
            showForNullSeries: false,
            showForZeroSeries: false,
          },
          yaxis: [
            {
              title: { text: UNIT.DEFAULT, style: { color: COLOR.DEFAULT, fontSize: YAXIS_TITLE_SIZE } },
              axisTicks: {
                color: COLOR.DEFAULT
              },
              axisBorder: {
                color: COLOR.DEFAULT
              },
              labels: {
                style: {
                  colors: [COLOR.DEFAULT],
                  fontSize: YAXIS_LABELS_SIZE
                }
              }
            },
            {
              opposite: true,
              title: { text: UNIT.DEFAULT, style: { color: COLOR.DEFAULT, fontSize: YAXIS_TITLE_SIZE } },
              axisTicks: {
                color: COLOR.DEFAULT
              },
              axisBorder: {
                color: COLOR.DEFAULT
              },
              labels: {
                style: {
                  colors: [COLOR.DEFAULT],
                  fontSize: YAXIS_LABELS_SIZE
                }
              }
            }
          ],
          xaxis: {
            type: XAXISTYPE,
            range: XAXISRANGE,
            labels: {
              format: 'mm:ss',
              style: {
                fontSize: XAXIS_LABELS_SIZE
              }
            },
            axisTicks: {
              color: COLOR.DEFAULT
            },
            axisBorder: {
              color: COLOR.DEFAULT
            }
          },
          grid: {
            borderColor: COLOR.DEFAULT
          },
        },
        series: [
          {
            name: '-',
            data: []
          },
          {
            name: '-',
            data: []
          }
        ] as ChartSeries[],
        recordings: [] as any[]
      }

    },
    methods: {
      resetChart() {
        console.log("reset chart");
        if (chartUpdator) {
          clearInterval(chartUpdator);
        }
        lastDate = 0;
        chartData = [];
        this.series = [];
      },
      startChartUpdator() {
        const self = this;
        chartUpdator = window.setInterval(() => {
          if (!self.latestValues) {
            return;
          }
          if (!chartData.length) {
            for (let j = 0; j < self.latestValues.length; j++) {
              chartData.push([]);
            }
          }
          if (!self.series.length) {
            for (let j = 0; j < self.latestValues.length; j++) {
              self.series.push({
                name: '-',
                data: []
              });
            }
          }
          const timeStamp = ++lastDate;// Math.floor(Date.now());

          if (self.latestValues && self.isSensorConnected) {

            const chartSeriesData = [];

            for (let i = 0; i < self.latestValues.length; i++) { // for each observable
              if (self.latestValues[i].type === "number") {
                chartData[i].push({ x: timeStamp, y: self.latestValues[i].value });
              } else {
                chartData[i].push({ x: timeStamp, y: self.latestValues[i].value.value });
              }
              chartSeriesData.push({ data: chartData[i], name: OBSERVABLE_FULL_NAME[self.latestValues[i].label] });
              self.series[i].data = chartData[i];
              self.series[i].name = self.latestValues[i].label;
            }
            (self.$refs.chart as any)?.updateSeries(chartSeriesData);

          }

        }, self.updatorInterval);
      },
      saveRecording() {

        const rec: Recording = {
          time: this.currentSessionStart?.format('h:mm a'),
          duration: lastDate,
          data: [],
          rawData: chartData,
        };

        for (let i = 0; i < chartData.length; i++) {
          const ys = chartData[i].map((d: { y: string }) => parseInt(d.y));
          const MIN: number = Math.min(...ys);
          const MAX: number = Math.max(...ys);
          const SUM: number = ys.reduce((a: any, b: any) => a + b, 0);
          const AVG: string = (SUM / ys.length).toFixed(2);
          const rd: RecordingData = { 
            label: this.series[i].name, 
            unit: UNIT[OBSERVABLE[this.series[i].name]],
            unitSymbol: UNIT_SYMBOL[OBSERVABLE[this.series[i].name]],
            avg: AVG, 
            min: MIN, 
            max: MAX 
          };
          rec.data.push(rd);
        }
        this.recordings.push(rec);

      },
      applyColor() {
        switch (this.sensorType) {
          case SENSOR_TYPE.CONDUCTIVITY:
            this.options.colors[0] = COLOR.CONDUCTIVITY;
            this.updateSingleYAxis(UNIT.CONDUCTIVITY, COLOR.CONDUCTIVITY);
            break;
          case SENSOR_TYPE.VOC:
            this.options.colors[0] = COLOR.VOC;
            this.updateSingleYAxis(UNIT.VOC, COLOR.VOC);
            break;
          case SENSOR_TYPE.HEART_RATE:
            this.options.colors[0] = COLOR.HEART_RATE;
            this.updateSingleYAxis(UNIT.HEART_RATE, COLOR.HEART_RATE);
            break;
          case SENSOR_TYPE.UV:
            this.options.colors[0] = COLOR.LUX;
            this.options.colors[1] = COLOR.UV;
            this.updateDuelYAxis(UNIT.LUX, COLOR.LUX, UNIT.UV, COLOR.UV);
            break;
          case SENSOR_TYPE.HUMIDITY:
            this.options.colors[0] = COLOR.TEMPERATURE;
            this.options.colors[1] = COLOR.HUMIDITY;
            this.updateDuelYAxis(UNIT.TEMPERATURE, COLOR.TEMPERATURE, UNIT.HUMIDITY, COLOR.HUMIDITY);
            break;
          case SENSOR_TYPE.TEMPERATURE:
            this.options.colors[0] = COLOR.AMBIENT_TEMP;
            this.options.colors[1] = COLOR.INFRARED_TEMP;
            this.updateDuelYAxis(UNIT.AMBIENT_TEMP, COLOR.AMBIENT_TEMP, UNIT.INFRARED_TEMP, COLOR.INFRARED_TEMP);
            break;
          case SENSOR_TYPE.TEMPERATURE2:
            this.options.colors[0] = COLOR.AMBIENT_TEMP;
            this.options.colors[1] = COLOR.INFRARED_TEMP;
            this.updateDuelYAxis(UNIT.AMBIENT_TEMP, COLOR.AMBIENT_TEMP, UNIT.INFRARED_TEMP, COLOR.INFRARED_TEMP);
            break;
          default:
            throw new Error(`Unexpected sensor type [${this.sensorType}]`);
        }
        this.colorApplied = true;
      },
      async connectSensor() {
        await serialService.connectAndReadAsync();
      },
      updateSingleYAxis(axisTitle: string, axisColor: string) {
        (this.$refs.chart as any)?.updateOptions({
          yaxis: [{
            title: { text: axisTitle, style: { color: axisColor, fontSize: YAXIS_TITLE_SIZE } },
            labels: {
              style: {
                colors: [axisColor],
                fontSize: YAXIS_LABELS_SIZE
              }
            }
          }]
        });
      },
      updateDuelYAxis(axisTitle1: string, axisColor1: string, axisTitle2: string, axisColor2: string) {
        (this.$refs.chart as any)?.updateOptions({
          yaxis: [
            {
              title: { text: axisTitle1, style: { color: axisColor1, fontSize: YAXIS_TITLE_SIZE } },
              labels: {
                style: {
                  colors: [axisColor1],
                  fontSize: YAXIS_LABELS_SIZE

                }
              }
            },
            {
              opposite: true,
              title: { text: axisTitle2, style: { color: axisColor2, fontSize: YAXIS_TITLE_SIZE } },
              labels: {
                style: {
                  colors: [axisColor2],
                  fontSize: YAXIS_LABELS_SIZE

                }
              }
            }
          ]
        });
      },
      recordValues() {
        this.isRecording = !this.isRecording;
        if (!this.isRecording) { // recording stopped
          if (chartUpdator) {
            clearInterval(chartUpdator);
          }
          this.saveRecording();
        } else {
          this.currentSessionStart = moment.default();// Math.floor(Date.now());
          this.resetChart();
          this.startChartUpdator();
        }
      },
      exportAllData() {
        for (let i = 0; i < this.recordings.length; i++) {
          this.exportData(i);
        }
      },
      exportData(index: number) {

        // find selected recording data //
        const selectedRecording: any = this.recordings[index];

        // create data row bucket //
        const rows = [];

        // add header row //
        const headerRow = [];
        headerRow.push("Time")
        console.log(selectedRecording);
        selectedRecording.data.forEach((element: RecordingData) => {
          const observableWithUnits = `${element.label} (${element.unitSymbol})`;
          headerRow.push(observableWithUnits);
        });
        rows.push(headerRow);

        // add data rows //
        for (let i = 0; i < selectedRecording.rawData[0].length; i++) {
          const dataRow = [];
          dataRow.push(selectedRecording.rawData[0][i].x)
          selectedRecording.rawData.forEach((element: { y: any }[]) => {
            dataRow.push(element[i].y);
          });
          rows.push(dataRow);
        }

        // generate csv file //  
        const csvContent = "data:text/csv;charset=utf-8," + rows.map(e => e.join(",")).join("\n");
        const encodedUri = encodeURI(csvContent);

        // prepare download link //
        const link = document.createElement("a");
        link.setAttribute("href", encodedUri);
        link.setAttribute("download", "kiwrious_data.csv");
        document.body.appendChild(link); // Required for FF

        // trigger auto download //
        link.click();

      },
      resetData() {
        // Alternatively, you can also reset the data at certain intervals to prevent creating a huge series 
        chartData[0] = chartData[0].slice(chartData[0].length - 10, chartData[0].length);
        chartData[1] = chartData[1].slice(chartData[1].length - 10, chartData[1].length);
      }
    },
    // watch: {
    //   sampleRate(){
    //     if(this.sampleRate){
    //       this.updatorInterval = 1000 / this.sampleRate;
    //     }
    //   }
    // },
    computed: {
      recordingButtonText() {
        if (this.isRecording) {
          return "Stop";
        } else {
          return "Record";
        }
      }
    }
  }
);


